html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  margin: 0;

  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: $darkest;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

@media (max-width: $mobile-middle-width) {
  body {
    font-size: 16px;
  }
}
